

export default {
    actions:{
       async weatherFetch({commit},position = 'kiev'){
            let dataWeather = await fetch(`https://api.weatherapi.com/v1/forecast.json?key=69f5dcd0f23c44a997743916220904&q=${position}&days=3&aqi=yes&alerts=yes`);
                dataWeather = await dataWeather.json();
                console.log(dataWeather);
                commit('updateWeather',dataWeather);
       }
    },
    mutations:{
        updateWeather(state,dataWeather){
            state.weather = dataWeather;
            //console.log('mutations Weather',state.weather);
        }
    },
    state:{
       weather:[]
    },
    getters:{
        allWeather(state){
            return state.weather
        },
        showWeaterTemp(state){
            for(let item in state.weather.current){
                    if (item == 'feelslike_c') {
                        return state.weather.current[item]
                }
                
            }
        },
        showWeaterImg(state){
            for(let item in state.weather.current){
                if (item == 'condition') {
                   return state.weather.current[item].icon
                }
            }
        }
    }
}